import React from 'react'

import { GatsbyImage, getImage } from "gatsby-plugin-image"

const Portraits = ({ frontmatter }) => {
  return (
    <section className="is-primary">
      <div className="container is-fluid my-4">
        <div className="content">
          <h3>Portraits</h3>
        </div>
          <div className="columns is-multiline">
              {frontmatter.portraits.map((portrait, index) => {
                  let image = null;
                  if (portrait.url) {
                      image = getImage(portrait.url.childImageSharp.thumb);
                  }
                  return (
                      <div key={index} className="column is-3 is-2-widescreen">
                          <div className="card">
                              <div className="card-image is-3by4">
                                  <GatsbyImage image={image} alt={portrait.name} />
                              </div>
                              <div className="card-content">
                                  <div className="media">
                                      <div className="media-content">
                                          <p className="title is-4">{portrait.name}</p>
                                          <p className="subtitle is-6">{portrait.position}{portrait.funktion} {portrait.jahrgang}</p>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  )
                  }
              )}
          </div>
      </div>
    </section>
  )
}

export default Portraits
