import * as React from "react"
import { StaticQuery, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

const Sponsoren = ({ data }) => (
    <div className="columns is-mobile is-multiline is-vcentered">
    {data.allMarkdownRemark.nodes.map((node, index) => {
      if (node.frontmatter.sponsor && node.frontmatter.sponsor.url) {
        return (
            <div key={index} className="column is-6-mobile is-2-tablet is-1-widescreen">
              <a href={node.frontmatter.sponsor.url}>
                <GatsbyImage
                    image={getImage(node.frontmatter.sponsor.image)}
                    alt={node.frontmatter.title}
                />
              </a>
            </div>
        );
      } else {
        return (
            <div key={index} className="column is-6-mobile is-2-tablet is-1-widescreen">
              <GatsbyImage
                  image={getImage(node.frontmatter.sponsor.image)}
                  alt={node.frontmatter.title}
              />
            </div>
        );
      }
    })}
    </div>
)

export default function MySponsoren(props) {
  return (
      <StaticQuery
          query={graphql`
    query Sponsoren {
      allMarkdownRemark (
        filter: {frontmatter: {slug: {regex: "/sponsoren\/"}}},
        sort: {frontmatter: {weight: ASC}}
      ) {
        nodes {
          frontmatter {
            title
            slug
            sponsor {
              type
              url
              image {
                childImageSharp {
                  gatsbyImageData(
                    layout: CONSTRAINED
                    width: 290
                    height: 190
                    placeholder: BLURRED
                    transformOptions: {fit:INSIDE, cropFocus:ATTENTION}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
            }
          }
        }
      }
    }
      `}
          render={data => <Sponsoren data={ data } {...props} />}
      />
  )
}

