import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import NavItem from "./navitem"
import Link from "../components/Link"
import { StaticImage } from "gatsby-plugin-image"

export default function Header() {
  const data = useStaticQuery(graphql`
  query menu {
    site {
      siteMetadata {
        title
        menus {
          main {
            url
            title
            children {
              title
              url
            }
          }
        }
      }
    }
  }
  `)

  function toggleBurgerMenu() {
    document.querySelector('.navbar-burger').classList.toggle('is-active');
    document.querySelector('.navbar-menu').classList.toggle('is-active');
  }

  return (
      <header>
        <nav className="navbar is-fixed-top is-dark" role="navigation" aria-label="main navigation">
          <div className="navbar-brand">
            <Link className="navbar-item" to="/">
              <StaticImage
                  fit={"inside"}
                  width={50}
                  alt="TV Zofingen Handball"
                  src="../assets/images/logo-icon.png"/>
              {data.site && data.site.siteMetadata.title}
            </Link>

            <button className="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbarTop"
                    onClick={toggleBurgerMenu}>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
              <span aria-hidden="true"></span>
            </button>
          </div>

          <div id="navbarTop" className="navbar-menu">
            <div className="navbar-end">
              {data.site && data.site.siteMetadata.menus.main.map((item, index) => (
                  <NavItem key={index} item={item}/>
              ))}
            </div>
          </div>
        </nav>
      </header>)
}
